@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Sedgwick+Ave&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{
  box-sizing:border-box; 
  
}
body{
  padding: 0px;
  margin:0px;
  background-color:#eeeeee;
}
.o-cont{
  max-width: 1200px;
  padding:15px;
  margin:0px auto;
  margin-bottom: 150px;
  color:rgb(249,220,62);
  background-color:#1d1b1b;
  background-color: #eeeeee;
  box-shadow: 15px 15px 15px #d3cece91,
              -15px -15px 15px #cac7c786;
  border-radius: 12px;            
}
.col{
  display:flex;
  justify-content:space-evenly;
  align-items:center;
  margin-top: 20px;
  padding:10px;
  background-color:rgb(50,51,48);
  border-radius:12px;

}
.text{
  font-size: 27px;
  text-align: center;
  font-family: 'Dancing Script', cursive;
}
.button-col
{
  font-size:12px;
  position: absolute;
  right: 40px;
}
.no-todos{
  font-size: 33px;
  text-align: center;
  font-family: 'Dancing Script', cursive;
  color:#1d1b1b;

}
.title
{
  font-size: 40px;
  text-align: center;
  font-family: 'Lobster', cursive;
  color:#1d1b1b;
  margin-top: 200px;
  margin-bottom: 80px;

}
.c-title
{
  text-align: center;
  font-family: 'Lobster', cursive;
  font-size: 32px;
  margin-bottom: 25px;
}
.foot
{
  margin-top: 100px;
   text-align: center;
}
#text
{
  margin-right: 10px;

}
#text:focus
{
  
  background-color: rgb(15, 14, 14);
  color: white;

}
.text-input
{
  text-align: center;
  width: 600px;
  padding:20px;
  margin:0px auto;
}


@media(max-width:768px)
{
  .d-btn
  {
    width:62px;
    text-align: center;
    font-size: 12px;
  }
  .text-input
  {
    width: 300px;

  }
  .text
  {
    font-size: 23px;

  }
  .button-col
{
  font-size:12px;
  position: absolute;
  right: 12px;

}

}
@media(max-width:380px)
{
  .o-cont{
    background-color:#eeeeee;
    box-shadow: none;
  }
  .text-input
  {
    width: 200px;

  }
  .d-btn
  {
    width:55px;
    text-align: center;
    font-size: 10px;
  }
  .button-col
{
  
  position: static;
  margin-left: 15px;

}
.text
{
  font-size: 16px;
}
.input-tag{
  width:100px;
  font-size: 10px;
}
.a-btn
{
  height: 30px;
  margin-top: 15px;
  font-size: 10px;

}
.foot{
  font-size: 12px;
}
}
